<template>
  <main class="content container" v-if="$store.state.loadingCart" style="position: absolute; z-index: 1000; background-color: white; text-align: center; justify-content: center; display: flex; align-items: center; width: 100%; height: 100%;">Загрузка корзины...
    <img src="/img/Fidget-spinner.gif">
  </main>
   
    <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ totalLength }} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            
             <CartItem v-for="item in products" :key="item.productId" :item="item"></CartItem>
            
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" v-show="products.length !== 0" :to="{name: 'order'}" class="cart__button button button--primery" type="submit">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
  import CartItem from '@/components/CartItem.vue'
  import products from '@/data/products';
  import numberFormat from '@/helpers/numberFormat';
  import router from '@/router';
  import { mapGetters } from 'vuex';

  export default {
    
    filters: { numberFormat },
    computed: {
        ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', totalLength: 'countLength' }),
    },
    components: { CartItem, router }
}
</script>